import React, { PropTypes } from 'react';
import axios from 'axios';
import Link from '../../components/Link'
class Home extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      data: {
        name: '',
        phone: '',
        address: '',
        product: this.props.selectProduct || 'com-kho-hoa-vang',
        quantity: ''
      }
    }
    this.order = this.order.bind(this)
  }

  order() {
    axios.post('/api/order/new', this.state.data)
      .then(res => {
        $('#myModal').modal({
          show: true
        })
        console.log(res)
      })
      .catch(err => {
        console.log(err)
      })
  }

  render() {
    return (
      <div >
        <div id="mainContent">
          <div className="container">
            <div className="row">
              <div className="col-md-6">
                <div className="form-horizontal">
                  <fieldset>
                    {/* Form Name */}
                    <legend >t hng</legend>
                    {/* Text input*/}
                    <div className="form-group">
                      <label className="col-md-4 control-label" htmlFor="product_id">H V Tn:</label>
                      <div className="col-md-8">
                        <input id="product_id" name="product_id" placeholder="H V Tn" className="form-control input-md" required type="text"
                          defaultValue={this.state.data.name}
                          onChange={(e) => {
                            let value = e.target.value
                            this.setState(prev => {
                              return {
                                ...prev,
                                data: {
                                  ...prev.data,
                                  name: value
                                }
                              }
                            })
                          }}
                        />
                      </div>
                    </div>
                    {/* Text input*/}
                    <div className="form-group">
                      <label className="col-md-4 control-label" htmlFor="product_name">S in thoi:</label>
                      <div className="col-md-8">
                        <input id="product_name" name="product_name" placeholder="S in thoi" className="form-control input-md" required type="text"
                               defaultValue={this.state.data.phone}
                               onChange={(e) => {
                                 let value = e.target.value
                                 this.setState(prev => {
                                   return {
                                     ...prev,
                                     data: {
                                       ...prev.data,
                                       phone: value
                                     }
                                   }
                                 })
                               }}
                        />
                      </div>
                    </div>
                    {/* Text input*/}
                    <div className="form-group">
                      <label className="col-md-4 control-label" htmlFor="product_description">a ch:</label>
                      <div className="col-md-8">
                        <textarea className="form-control" id="product_description" name="product_description"
                            defaultValue={this.state.data.address}
                            onChange={(e) => {
                              let value = e.target.value
                              this.setState(prev => {
                                return {
                                  ...prev,
                                  data: {
                                    ...prev.data,
                                    address: value
                                  }
                                }
                              })
                            }}
                        />
                      </div>
                    </div>
                    <div className="form-group">
                      <label className="col-md-4 control-label" htmlFor="product_categorie">Sn phm:</label>
                      <div className="col-md-8">
                        <select id="product_categorie" name="product_categorie" className="form-control"
                                defaultValue={this.state.data.product}
                                onChange={(e) => {
                                  let value = e.target.value
                                  this.setState(prev => {
                                    return {
                                      ...prev,
                                      data: {
                                        ...prev.data,
                                        product: value
                                      }
                                    }
                                  })
                                }}
                        >
                          <option value="com-kho-hoa-vang">Cm kh hoa vng</option>
                          <option value="cha-com">Ch Cm</option>
                          <option value="com-non-me-tri">Cm Non M Tr</option>
                        </select>
                      </div>
                    </div>
                    {/* Text input*/}
                    <div className="form-group">
                      <label className="col-md-4 control-label" htmlFor="available_quantity">S lng (kg):</label>
                      <div className="col-md-8">
                        <input id="available_quantity" name="available_quantity" placeholder="VD: 1kg" className="form-control input-md" required type="text"
                               defaultValue={this.state.data.quantity}
                               onChange={(e) => {
                                 let value = e.target.value
                                 this.setState(prev => {
                                   return {
                                     ...prev,
                                     data: {
                                       ...prev.data,
                                       quantity: value
                                     }
                                   }
                                 })
                               }}
                        />
                      </div>
                    </div>

                    <div className="form-group">
                      <label className="col-md-4 control-label" htmlFor="singlebutton" />
                      <div className="col-md-8">
                        <button id="singlebutton" name="singlebutton" className="btn btn-primary"
                          onClick={this.order}
                        >t hng</button>
                      </div>
                    </div>

                    <legend>Lin h</legend>
                    <p>Hotline : <b style={{color: 'red'}}>0973.318.868</b></p>
                    <p>a ch : 128  Ch M Tr  Qun Nam T Lim  H Ni</p>
                  </fieldset>
                </div>
              </div>
              <div className="col-md-6">
                <fieldset>
                  <legend>Hnh thc thanh ton</legend>
                  <p> thun li hn cho qu khch mua Cm, chng ti c 2 hnh thc thanh ton sau :</p>
                  <p>Khch hng  gn (bn knh 20km) : Qu khch thanh ton bng chuyn khon hoc tin mt sau khi nhn cm .</p>
                  <p>Khch hng  xa (trn 20km) : Qu khch vui lng thanh ton 100% n hng trc khi nhn hng.</p>
                  <p><b>Thng tin ti khon</b></p>
                  <p>Ch ti khon : <b style={{color: 'red'}}>Nguyn Hu T</b></p>
                  <p>S ti khon : <b style={{color: 'red'}}>84387487</b></p>
                  <p>Ngn hng: <b style={{color: 'red'}}>VPBank chi nhnh ng </b></p>
                  <br/>
                  <p>Hotline : <b style={{color: 'red'}}>0973.318.868</b></p>
                  <p>a ch : 128  Ch M Tr  Qun Nam T Lim  H Ni</p>
                </fieldset>
              </div>
            </div>
          </div>

          <Modal />
        </div>
      </div>
    );
  }
}

const Modal = () => {
  return (
    <div id="myModal" className="modal fade" role="dialog">
      <div className="modal-dialog">
        {/* Modal content*/}
        <div className="modal-content">
          <div className="modal-header">
            <button type="button" className="close" data-dismiss="modal"></button>
            <h4 className="modal-title">Modal Header</h4>
          </div>
          <div className="modal-body">
            <p>
              t hng thnh cng, chng ti s sm lin lc vi bn, nu c g thc mc xin ln h qua s in thoi t vn v h tr <b style={{color: 'red'}}>0973.318.868</b>
            </p>
          </div>
          <div className="modal-footer">
            <Link to={'/san-pham'} className="btn btn-primary">Trang sn phm</Link>
            <button type="button" className="btn btn-default" data-dismiss="modal">ng ca s</button>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Home



